.home__hero-section {
    /* color: #fff; */
    /* padding: 160px 0; */
    display: block;
    width: inherit;
    /* align-items: stretch;
    justify-content: stretch;
    width: 100%; */
  }

  .lightbg{
    background-color: white;
  }
  
  .home__hero-row {
    align-items: center;
  }
  /* .container{
    justify-content: center;
    align-self: center;
  } */
  .row {
    display: flex;
    /* margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px; */
    flex-wrap: wrap;
    align-content: stretch;
  }
  
  .col {
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    flex: 1;
    max-width: 50%;
    flex-basis: 50%;
  }
  
  .home__hero-text-wrapper {
    /* max-width: 540px; */
    padding: 15px 0 0 0;
    /* padding-bottom: 60px; */   
  }
  
  .top-line {
    color: #f00946;
    font-size: 26px;
    line-height: 16px;
    font-weight: 800;
    letter-spacing: 1.4px;
    /* text-transform: uppercase; */
    padding-bottom: 30px;    
    text-align: center;
    padding-top: 25px;
    font-variant-caps: petite-caps;
  }
  
  .heading {
    margin-bottom: 24px;
    font-size: 48px;
    line-height: 1.1;
    font-weight: 600;
    /* color: #f7f8fa; */
  }
  
  .dark {
    color: #2a371c;
  }
  
  .darkBg {
    background-color: #1c2237;
    /* #1c2237; */
  }
  
  .home__hero-subtitle {
    max-width: 440px;
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
  }
  
  .home__hero-img-wrapper {
    /* max-width: 555px; */
    align-items: center;
    justify-content: center;
  }
  
  .home__hero-img {
    /* max-width: 95%; */
    margin-top: 0;
    margin-right: 0;
    margin-left: 10px;
    padding-right: 0;
  }
  
  img {
    border: 0;
    max-width: 100%;
    vertical-align: middle;
    display: inline-block;
  }
  
  @media screen and (max-width: 991px) {
    .container {
      padding-right: 30px;
      padding-left: 30px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .home__hero-text-wrapper {
      padding-bottom: 2%;
    }
    .top-line {
      font-size: initial;
      padding: 15px 0 15px 0;
    }
    .col {
      max-width: 100%;
      flex-basis: 100%;
    }
  }
  